body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 105vh;
  width: 100%;
  background-color: #282c34;
}

.calc-wrapper {
  width: 400px;
  height: 600px;
}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}
.button-wrapper {
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    color: #888888;
    background-color: #E0E1E6;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}

.button-wrapper:hover {
    background-color: #A9A9A9;
    color: #FFFFFF;
    cursor: pointer;
}

.operator {
    background-color: #FE9241;
    color: #FFFFFF;
}

.operator:hover{
    background-color: #FF8C00;
}
.input {
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    /* font-weight: bold; */
    font-size: 1.4em;
    background-color: #000000;
    color: #FFFFFF;
    padding: 0.5em;
    outline: 1px solid #888888;
}
.clear-btn {
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #C5C3CD;
    -webkit-flex: 1 1;
            flex: 1 1;
    color: #000000;
    outline: 1px solid #888888;
}

.clear-btn:hover {
    cursor: pointer;
    color: #FFFFFF;
    background-color: #888888;
}
